import Checkbox from '../u-checkbox.vue';

export default {
    name: 'u-checkbox--0',
    extends: Checkbox,
    watch: {
        currentValue(value) {
            this.watchValue(value);
        },
    },
    mounted() {
        this.watchValue(this.value);
    },
    methods: {
        watchValue(value) {
            this.$refs.checkbox.indeterminate = value === null;
        },
    },
};



